$shadow-bottom: 0px 13px 12px -5px rgba(0, 0, 0, 0.88);
$shadow: 0px 3px 10px 6px rgba(0, 0, 0, 0.1);
$shadow-sm: 1px 1px 2px 2px rgba(211, 211, 211, 0.307);
$secondary: #bc3fea;
$bg: #020710;
$text: #738296;

@mixin position($align: center, $justify: center, $flow: row, $wrap: no-wrap) {
	display: flex;
	align-items: $align;
	justify-content: $justify;
	flex-direction: $flow;
	flex-wrap: $wrap
}

$breakpoints: (
	'sm': 700px,
	'md': 850px,
	'lg': 1000px,
	'xl': 1250px,
);

@mixin sm {
	@media (max-width: map-get($breakpoints, 'sm')) {
		@content;
	}
}
@mixin md {
	@media (max-width: map-get($breakpoints, 'md')) {
		@content;
	}
}
@mixin lg {
	@media (max-width: map-get($breakpoints, 'lg')) {
		@content;
	}
}
@mixin xl {
	@media (max-width: map-get($breakpoints, 'xl')) {
		@content;
	}
}

@mixin media($bp) {
	@media (max-width: $bp) {
		@content;
	}
}
